/*
 * Common job, group and count styling
 */

.job-group {
  margin: 0;
  cursor: default;
}

.job-btn {
  background: transparent;
  padding: 0 2px 0 2px;
  vertical-align: 0;
  line-height: 1.32;
  display: none;
  transition: transform 0.1s;
  font-size: 12px;
}

.group-btn {
  background: transparent;
  padding: 0 2px 0 2px;
  vertical-align: 0;
  line-height: 1.32;
  cursor: pointer;
}

.group-btn::before {
  content: '+';
}

.group-btn.btn.job-group-count:hover {
  background-color: rgba(219, 235, 252, 0.51);
}

.group-symbol {
  background: transparent;
  padding: 0 2px 0 2px;
  border: 0;
  vertical-align: 0;
  line-height: 1.32;
  cursor: pointer;
  margin-right: -3px;
}

.group-symbol:hover {
  cursor: pointer;
}

.group-content {
  margin: 0;
  white-space: nowrap;
}

.group-content::before {
  content: '(';
  padding-left: 2px;
  padding-right: 2px;
}

.group-content::after {
  content: ')';
  margin-right: 2px;
  padding-left: 2px;
}

.group-job-list {
  margin-left: -3px;
  white-space: normal;
}

.runnable-job-btn {
  display: none;
  color: rgba(128, 128, 0, 0.5);
  margin: 0 -2px 0 -1px;
  background: transparent;
  padding: 0 2px;
  vertical-align: 0;
  line-height: 1.32;
  font-size: 12px;
  border-radius: 0;
  text-align: center;
  white-space: nowrap;
}

.runnable-job-btn-selected {
  border: 1px solid;
  padding: 0 -1px;
  border-radius: 3px;
  margin: 0 -2px 1px 0;
  overflow: visible;
}

.selected-job {
  border: 4px solid;
  background-color: #fff;
}

.selected-count {
  background-color: #ffffffb3;
}

.btn-lg-xform {
  margin: -2px 0px !important;
  border: 2px solid;
  border-radius: 3px;
  font-size: 12px;
  z-index: 1;
  transition: 0.1s;
  transform: scale(1.3, 1.3);
}

.btn-lg-xform:hover {
  opacity: 0.9;
}

.filter-shown {
  display: inline-block;
}

